* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  width: 100%;
  height: 120px;
  background-color: #DCDCDC;
}
.main {
  width: 100%;
  height: 100%;
}
.footer {
  width: 100%;
  height: 120px;
  background-color: #DCDCDC;
}
.slider {
  width: 100%;
  padding: 20px 20px;
  display: grid;
  grid-gap: 20px;
  justify-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
  background-color: #ffffff;
}
@media (max-width: 600px) {
  .slider {
    width: 100%;
    padding: 20px 0;
  }
}
.sleder-controls {
  height: min-content;
  position: relative;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(4, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.sleder-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #CCFFCC;
}
.sleder-controls__button {
  width: 80px;
  height: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.sleder-controls__button-img {
  width: 100%;
  height: 100%;
}
.sleder-controls__button-img:hover {
  opacity: 0.7;
}
.sleder-controls__button-img:active {
  opacity: 0.3;
}
.sleder-controls__total {
  font-weight: bold;
  font-family: 'Lato', sans-serif;
}
.sleder-controls__total--current {
  font-size: 23px;
  align-self: start;
}
.sleder-controls__total--total {
  font-size: 18px;
  opacity: 0.7;
  align-self: end;
}
.slider-items {
  width: 500px;
}
@media (max-width: 600px) {
  .slider-items {
    width: 100%;
  }
}
.slider-items__item {
  text-align: center;
}
.slider-items__item--hidden {
  display: none;
}
.slider-items__img {
  width: 500px;
  height: 200px;
  object-fit: cover;
}
@media (max-width: 600px) {
  .slider-items__img {
    width: 100%;
  }
}
.sliderfull {
  position: relative;
  width: 100%;
  padding: 20px 20px;
  display: grid;
  grid-gap: 10px;
  justify-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
  background-color: #F5F5F5;
}
@media (max-width: 600px) {
  .sliderfull {
    padding: 20px 0;
  }
}
.slederfull-controls {
  height: min-content;
  position: relative;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(2, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.slederfull-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #CCFFCC;
}
.slederfull-controls__total {
  font-weight: bold;
  font-family: 'Lato', sans-serif;
}
.slederfull-controls__total--current {
  font-size: 23px;
  align-self: start;
}
.slederfull-controls__total--total {
  font-size: 18px;
  opacity: 0.7;
  align-self: end;
}
.sliderfull-items__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 1;
}
.sliderfull-items__button--next {
  right: 10px;
}
.sliderfull-items__button--prev {
  left: 10px;
}
.sliderfull-items__button-img {
  width: 100%;
  height: 100%;
}
.sliderfull-items__button-img:hover {
  opacity: 0.7;
}
.sliderfull-items__button-img:active {
  opacity: 0.3;
}
.sliderfull-indicators {
  z-index: 1;
  max-width: 350px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
}
.sliderfull-indicators__item {
  list-style: none;
}
.sliderfull-indicators__button {
  margin: 0 3px;
  width: 40px;
  height: 10px;
  border: none;
  border-radius: 8px;
  background-color: #fff;
  opacity: 0.5;
  transition: 0.4s ease;
  cursor: pointer;
}
@media (max-width: 600px) {
  .sliderfull-indicators__button {
    width: 30px;
    height: 10px;
  }
}
.sliderfull-indicators__button--active {
  background-color: tomato;
  opacity: 1;
}
.sliderfull-indicators__button:hover,
.sliderfull-indicators__button:focus {
  opacity: 0.8;
}
.sliderfull-indicators__button:active {
  transition: 0s;
  opacity: 1;
}
.sliderfull-items {
  width: 500px;
  overflow: hidden;
  position: relative;
}
@media (max-width: 600px) {
  .sliderfull-items {
    width: 100%;
  }
}
.sliderfull-items__inner {
  display: flex;
  transition: 0.6s all;
}
.sliderfull-items__item {
  text-align: center;
}
.sliderfull-items__img {
  width: 500px;
  height: 200px;
  object-fit: cover;
}
@media (max-width: 600px) {
  .sliderfull-items__img {
    width: 100%;
  }
}
.tabs {
  padding: 20px 20px;
  display: grid;
  grid-template-rows: 1fr min-content;
  grid-row-gap: 20px;
  justify-items: center;
}
.tab {
  display: none;
}
.tab--current {
  display: block;
}
.tab__wrap {
  display: grid;
  grid-template-columns: 100px 500px;
  grid-column-gap: 25px;
  width: 100%;
}
@media (max-width: 622px) {
  .tab__wrap {
    grid-template-rows: min-content 1fr;
    grid-template-columns: 1fr;
    grid-gap: 10px;
  }
}
.tab__title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}
@media (max-width: 622px) {
  .tab__title {
    align-items: center;
  }
}
.tab__img {
  align-self: center;
  object-fit: contain;
  background-color: #fff;
  width: 100%;
  height: 200px;
}
@media (max-width: 622px) {
  .tab__img {
    width: 100%;
    height: 200px;
  }
}
/*tabs controls radio btn styles*/
.tabs-controls {
  justify-self: center;
  border: none;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(3, min-content);
  grid-column-gap: 20px;
  margin: 0;
  padding: 0;
}
.tabs-controls__btn {
  position: absolute;
  appearance: none;
}
.tabs-controls__btn:checked ~ .tabs-controls__custom-elem::before {
  position: absolute;
  content: '';
  width: 25px;
  height: 25px;
  background-color: #C0C0C0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}
.tabs-controls__btn[data-tab-name="tab-3"]:checked ~ .tabs-controls__custom-elem::before {
  background-color: #FFFF00;
}
.tabs-controls__btn[data-tab-name="tab-2"]:checked ~ .tabs-controls__custom-elem::before {
  background-color: #0000FF;
}
.tabs-controls__btn[data-tab-name="tab-1"]:checked ~ .tabs-controls__custom-elem::before {
  background-color: tomato;
}
.tabs-controls__label {
  display: flex;
}
.tabs-controls__custom-elem {
  width: 48px;
  height: 48px;
  margin-right: 23px;
  background-color: #AFEEEE;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  margin: 0;
}
.popup-block {
  width: 100%;
  height: 300px;
  background-color: #F5F5F5;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup-block__btn {
  width: 200px;
  height: 80px;
  border: 2px solid #DCDCDC;
  border-radius: 5px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 22px;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #ffffff;
  transition: 0.5s all;
}
.popup-block__btn:hover,
.popup-block__btn:focus {
  font-size: 25px;
  border: 2px solid #CCFFCC;
}
.popup-block__btn:active {
  transition: 0s all;
  opacity: 0.7;
}
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
.popup--target {
  opacity: 1;
  visibility: visible;
}
.popup__area {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2;
}
.popup__content {
  position: relative;
  width: 400px;
  height: 200px;
  background-color: #ffffff;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 600px) {
  .popup__content {
    width: 60%;
  }
}
.popup__close-btn {
  width: 30px;
  height: 30px;
  position: absolute;
  right: -40px;
  top: 0;
  background-color: transparent;
  opacity: 0.8;
  border: none;
  cursor: pointer;
  transition: 0.6s all;
}
.popup__close-btn:hover,
.popup__close-btn:focus {
  transform: rotate(180deg);
  opacity: 1;
}
.popup__close-img {
  width: 100%;
  height: 100%;
}
